.radio-button {  
  display: flex;
  flex-direction: column;
  background: var(--color-secondary);
  cursor: pointer;
  position: relative;
  text-align: center;
  background: transparent;
  //margin: 0 .4em 0 .4em;
}

.image {
  &:hover {
    opacity: 1;
  }
  background: transparent;
  opacity: .4;
  border-radius: 40%;
  padding: .8em;
  //width: 75%;
}

.selected {
  opacity: 1;
}

.text {
  font-family: 'Cedarville Cursive', cursive;
  font-size: x-large;
  margin-top: -.5em;
  //color: var(--color-secondary);
}